.tradeMarker {
  stroke: var(--trade-color);
  fill: var(--trade-color);
  stroke-width: 0;
  pointer-events: none;
}
.tradeMarker.focal {
  stroke-width: 2px;
}
.tradeMarker.dimmed {
  fill: var(--trade-color-dimmed);
  stroke: var(--trade-color-dimmed);
}
.tradeLine {
  stroke: var(--trade-color);
  stroke-dasharray: 4;
}
